import { Component, OnInit, OnDestroy, TemplateRef } from '@angular/core';
import { ApiService } from '../../api.service';
import { Subject } from 'rxjs';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ActivatedRoute, NavigationStart, NavigationExtras, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'app-radio-program',
  templateUrl: './radio-program.component.html',
  styleUrls: ['./radio-program.component.css']
})
export class RadioProgramComponent implements OnInit {
  data: any = [];
  dtOptions: DataTables.Settings = {};
  // We use this trigger because fetching the list of persons can be quite long,
  // thus we ensure the data is fetched before rendering
  dtTrigger = new Subject();
  modalRef: BsModalRef;
  message: string = '';
  deleteItem: any = 0;
  appstate$: Observable<object>;

  constructor(
  	public api: ApiService,
    public modalService: BsModalService,
    public router: Router
  	) { 

  }

  ngOnInit(): void {
    this.appstate$ = this.router.events.pipe(
      filter(e => e instanceof NavigationStart),
      map(() => {
        const currentState = this.router.getCurrentNavigation();
        return currentState.extras.state;
      })
    );
  	this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5
    };
    this.api.provideData({
  		type: 'getprogramradioadm'
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.data = resp;
  		console.log(this.data);
  		this.dtTrigger.next();
  	});

    
  }

  getData(){
  	this.api.provideData({
  		type: 'getprogramradioadm'
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.data = resp;
  		console.log(this.data);
  		
  	});
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }

  deleteProgram(item){
  	this.api.provideData({
  		type: 'deleteprogramradioadm',
  		program_id: item.program_id,
  		program_flyer_ori: item.program_flyer_ori
  	}, 'data-adm-api').subscribe((resp)=>{
  		console.log(resp);
  		this.getData();
  	});
  }

   openModal(template: TemplateRef<any>, item) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
    this.deleteItem = item;
  }

  confirm(): void {
    this.message = 'Confirmed!';
    this.deleteProgram(this.deleteItem);
    this.modalRef.hide();
  }
 
  decline(): void {
    this.message = 'Declined!';
    this.modalRef.hide();
  }

  editProgram(item){
    let objToSend: NavigationExtras = item;
    this.router.navigate(['/radio-programedit'], {state:  { data: objToSend }});
  }

}
